.main {
  padding: 88px 0;
}

.main-inner {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  column-gap: 120px;
  row-gap: 60px;
}

.item {
  max-width: 375px;
}

.item img {
  margin-bottom: 10px;
  border: solid 4px #000;
  border-radius: 10px;
  box-shadow: 10px 4px 0px 0px rgba(0, 0, 0, 0.2);

  transition: all 0.1s ease-in;
}

.item img:hover {
  transform: scale(1.01);
}

.item h3 {
  font-size: 35px;
}

/* ------------ media query ------------ */

@media (max-width: 1680px) {
  .main {
    padding: 75px 0;
  }
  .main-inner {
    column-gap: 70px;
  }

  .item {
    max-width: 320px;
  }

  .item h3 {
    font-size: 30px;
  }
}

@media (max-width: 1370px) {
  .main {
    padding: 50px 0;
  }

  .main-inner {
    column-gap: 90px;
  }

  .item {
    max-width: 250px;
  }

  .item h3 {
    font-size: 20px;
  }
}

@media (max-width: 1241px) {
  .main-inner {
    column-gap: 70px;
  }

  .item {
    max-width: 230px;
  }
}

@media (max-width: 1050px) {
  .main-inner {
    column-gap: 50px;
  }

  .item {
    max-width: 210px;
  }
}

@media (max-width: 930px) {
  .main-inner {
    column-gap: 100px;
  }
}

@media (max-width: 690px) {
  .main-inner {
    row-gap: 30px;
    column-gap: 60px;
  }

  .item {
    max-width: 160px;
  }
}
