.header {
  background-color: #000;
  color: #fff;
  padding: 20px 0;
}

.header-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-icon {
  display: none;
}

.header-logo a {
  font-weight: 600;
  font-size: 30px;
  text-transform: uppercase;
  color: #fff;

  transition: all 0.2s ease-in;
}

.header-logo a:hover {
  color: #9f9f9f;
}

.header-nav {
  display: flex;
  align-items: center;
  column-gap: 70px;
}

.header-nav ul {
  display: flex;
  column-gap: 10px;
  align-items: center;
}

.header-nav a {
  text-transform: uppercase;
  font-size: 30px;
  color: #fff;

  transition: all 0.2s ease-in;
}

.header-nav a:hover {
  color: #9f9f9f;
}

.header-nav img {
  width: 35px;
  cursor: pointer;

  transition: all 0.2s ease-in;
}

.header-nav img:hover {
  transform: scale(1.04);
}

/* ------------ media query ------------ */

@media (max-width: 1370px) {
  .header-logo a {
    font-size: 25px;
  }

  .header-nav a {
    font-size: 20px;
  }

  .header-nav img {
    width: 25px;
  }
}

@media (max-width: 1050px) {
  .header-logo a {
    font-size: 20px;
  }

  .header-nav a {
    font-size: 16px;
  }

  .header-nav img {
    width: 20px;
  }
}

@media (max-width: 930px) {
  .menu-icon {
    cursor: pointer;
    display: block;
    z-index: 2;
  }

  .menu-icon.fixed {
    position: fixed;
  }

  .header-nav ul {
    position: fixed;
    top: 0;
    right: -180px;
    bottom: 0;
    background-color: #4e4e4e;
    padding-top: 70px;

    display: flex;
    flex-direction: column;
    row-gap: 40px;

    width: 180px;
    transition: 0.5s;
  }

  .header-nav ul.hide-mobile-menu {
    right: 0px;
  }
}

@media (max-width: 690px) {
  .header-logo a {
    font-size: 18px;
  }
}
