.preview {
  padding: 90px 0 150px;
}

.preview-wrapper {
  display: flex;
  justify-content: center;
  column-gap: 120px;
}

.preview-img {
  width: 500px;
}

.preview-img img {
  cursor: pointer;
  border: solid 4px #000;
  border-radius: 10px;
  box-shadow: 10px 4px 0px 0px rgba(0, 0, 0, 0.2);

  transition: all 0.2s ease-in;
}

.preview-img img:hover {
  transform: scale(1.01);
}

.preview-text {
  max-width: 592px;
}

.preview-text h2 {
  line-height: 0.9;
  margin-bottom: 15px;
  font-size: 60px;
  font-weight: 600;
}

.preview-inner {
  margin-bottom: 20px;
}

.item {
  font-weight: 700;
  margin-bottom: 17px;
  font-size: 30px;
}

.item:last-child {
  margin-bottom: 0;
}

/* ---------- media query ------------ */

@media (max-width: 1400px) {
  .preview {
    padding: 40px 75px;
  }
  .preview-wrapper {
    column-gap: 80px;
  }

  .preview-text {
    max-width: 450px;
  }
  .item {
    font-size: 20px;
  }

  .preview-img {
    width: 400px;
  }
}

@media (max-width: 1125px) {
  .preview-wrapper {
    column-gap: 40px;
  }
  .preview-img {
    width: 375px;
  }

  .preview-text {
    max-width: 400px;
  }

  .preview-text h2 {
    margin-bottom: 5px;
  }

  .preview-inner {
    margin-bottom: 10px;
  }

  .item {
    margin-bottom: 10px;
    font-size: 18px;
  }
}

@media (max-width: 984px) {
  .preview-wrapper {
    flex-direction: column;
    align-items: center;
  }

  .preview-img {
    width: 250px;
  }

  .preview-text {
    text-align: center;
    width: 300px;
  }

  .preview-text h2 {
    font-size: 40px;
  }

  .items {
    align-items: center;
    display: flex;
    flex-direction: column;
  }
}
